import {GetUserAskTopList} from 'api/user'
import UniLoadMore from '@/components/uni/uni-load-more/uni-load-more.vue'

export default {
	data() {
		return {
			loaded:false,
			tabs:['全部','未解决','已解决'],
			index:0,
			list:[],
			getParameters:{
				flag:-1,
				showstatus:-1,
				page:1,
				pagesize:10,
				channelid:-1,
				mytype:1
			},
			pageCount:0,
			moreStatus:'more',
			classList:[
				{id:-1,name:'全部'},
				{id:1,name:'文章'},
				{id:10000,name:'问答'},
				{id:10001,name:'班级'},
				{id:10002,name:'课程'},
				{id:10003,name:'教师'},
				{id:10004,name:'播放页'}
			]
		}
	},
	components:{
		UniLoadMore
	},
	onReachBottom(e){
		this.getParameters.page++
		this.moreList()
	},
	onPullDownRefresh() {
		this.loadPage()
	},
	onLoad() {
		uni.showLoading({
		    title: '加载中'
		});
		this.$store.ready(state=> {
			this.loadPage(); 
		})
	},
	methods: {
		changeTabs(e){
			if(e.tab=='全部'){
				this.getParameters.showstatus = -1
			}else if(e.tab=='未解决'){
				this.getParameters.showstatus = 0
			}else{
				this.getParameters.showstatus = 1
			}
			
			this.loadPage()
		},
		setClassIndex(id){
			this.getParameters.flag = id
			this.getParameters.channelid = id
			this.loadPage()
		},
		open(item){
			var url = encodeURIComponent(item.view_url)
			if(item.flagid==1||item.flagid==10001||item.flagid==10002||item.flagid==10004){
				uni.navigateTo({
					url:'/pages/web/web?title='+item.title+'&photo='+item.defaultpic+'&url='+url
				})
			}else if(item.flagid==10000){
				uni.navigateTo({
					url:'/pages/class-center/ask/details?topicid='+item.topicid
				})
			}else if(item.flagid==10003){
				uni.navigateTo({
					url:'/pages/teacher/details?tcid='+item.topicid
				})
			}
			
		},
		moreList(){
			if(this.getParameters.page > this.pageCount){
				this.moreStatus = 'noMore'
			}else{
				this.moreStatus = 'loading'
				this.loadPage('more')
			}
			
		},
		loadPage(status){
			if(status=='more'){
				GetUserAskTopList(this.getParameters).then(res=>{
					this.moreStatus = 'more'
					if(res.result){
						res.data = res.data.filter(item=>{//下载先隐藏
							if(item.flag_str!='下载'){
								return item
							}
						})
						this.list = this.list.concat(res.data)
					}
					this.loaded = true
				})
			}else{
				this.getParameters.page = 1
				GetUserAskTopList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pagecount
						if(this.pageCount==1){
							this.moreStatus = 'noMore'
						}else{
							this.moreStatus = 'more'
						}
						this.list = res.data.filter(item=>{
							if(item.flag_str!='下载'){
								return item
							}
						})
					}else{
						this.list = []
						this.pageCount = 0
					}
					this.loaded = true
					uni.stopPullDownRefresh();
					uni.hideLoading();
				})
			}
		}
	}
}
